<template>
	<view class="container">
		
		<view class="register">登录</view>
		<view class="registerBox">
			<form @submit="formSubmit($event)">
				<input name="jobNumber" placeholder="账号" class='accountNumber'></input>
				<input name="password" password placeholder="密码" class='password'></input>
				<button form-type="submit" class='registerButton'>登录</button>
				<uni-popup ref="message" type="message">
				<uni-popup-message :type="msgType" :message="messageText" :duration="2000"></uni-popup-message>
				</uni-popup>
			</form>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				    jobNumber:'',
					password:'',
					type: 'center',
					msgType: 'error',
					messageText: '',
					value: ''
			}
		},
		methods: {
           formSubmit(e){
			 this.jobNumber = e.target.value.jobNumber,
			 this.password = e.target.value.password
		  const self = this
		  uni.request({
		  method:"post",
		  url: this.$api+'/api/user/wechatUserLogin',
		  data:{
			  jobNumber:this.jobNumber,
			  password:this.password
		  },
		  header: {
		  'content-type': 'application/json' 
		  },
		  success: (res) => {
			  
		  // 请求成功之后
		  if(res.data.code===200)
	        {
		    console.log(res)
		    uni.setStorage({
		        key:'token',
		        data:res.data.data,
		        success() { 
		        uni.switchTab({  url: '/pages/list/list'});
		      }
		       });
			   uni.setStorage({
				   key:'username',
				   data:self.jobNumber,
			   })
			   uni.setStorage({
			   	key:'oldPassword',
				data:self.password
			   })
		   }
		   else {
			    const val = res.data.msg 
				self.messageText = res.data.msg 
				self.$refs.message.open()		
		   }
		   }
		})
		},
	},
	}
</script>

<style lang="scss">
	.container {
		padding: 20px;
		font-size: 14px;
		line-height: 24px;
	    margin-top:50px;
	}
	.register{
		font-weight: 500;
		font-size: 28px;
		text-align: center;
	}
	.registerBox{
		display: flex;
		justify-content: center;
		padding: 15px;
	}
	.accountNumber,.password{
		width: 233px;
		height:30px;
		font-size: 14px;
		border-bottom:1px solid #BBBBBB;
		color:#888888;
		padding: 4px;
	}
	.checkText{
	   font-size: 10px;
	   padding: 10px;
	}
	.checkBox{
		transform:scale(0.6);
		// color:$uni-bg-color;
	}
	.registerButton{
		width:80px;
		height:30px;
		background-color: $uni-bg-color;
		color:#FFFFFF;
		line-height: 30px;
		font-size: 14px;
		margin-top:15px;
	}
	.forgetPassword{
		font-size: 12px;
		color:$uni-bg-color;
		display: flex;
		justify-content: flex-end;
		padding: 10px;
	}
</style>
